import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TemporaryPurchaseInvoiceComponent } from './temporary-purchase-invoice.component';
import { EditTemporaryPurchaseComponent } from './edit-temporary-purchase/edit-temporary-purchase.component';
import { DetailComponent } from './detail/detail.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { TaxComponent } from './components/tax/tax.component';
import { InfoSupplierComponent } from './components/info-supplier/info-supplier.component';
import { SelectSupplierComponent } from './components/select-supplier/select-supplier.component';
import { StaffComponent } from './components/staff/staff.component';
import { SearchModule } from 'src/app/cafe/components/search/search.module';
const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: "",
        component: TemporaryPurchaseInvoiceComponent
      },
      {
        path: "detail",
        component: DetailComponent
      },
      {
        path: "edit-temporary",
        component: EditTemporaryPurchaseComponent,
      },
    ]
  },

]

@NgModule({
  declarations: [
    TemporaryPurchaseInvoiceComponent,
    EditTemporaryPurchaseComponent,
    DetailComponent,
    StaffComponent, SelectSupplierComponent, TaxComponent, InfoSupplierComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    ScrollingModule,
    SearchModule
  ]
})
export class TemporaryPurchaseInvoiceModule { }
