import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhQueryCafe, VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { Observable } from 'rxjs';
import { PurchaseA4Component } from 'src/app/cafe/components/print/purchase/purchase-a4/purchase-a4.component';
import { PurchaseA5Component } from 'src/app/cafe/components/print/purchase/purchase-a5/purchase-a5.component';
import { PurchaseK80Col4Component } from 'src/app/cafe/components/print/purchase/purchase-k80-col4/purchase-k80-col4.component';
import { PurchaseK80Col3Component } from 'src/app/cafe/components/print/purchase/purchase-k80-col3/purchase-k80-col3.component';
import { PurchaseK57Col3Component } from 'src/app/cafe/components/print/purchase/purchase-k57-col3/purchase-k57-col3.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhPurchaseInvoices, VhPurchaseInvoiceDetail } from 'src/app/cafe/interface/vh-purchase-invoice';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NzModalService } from 'ng-zorro-antd/modal';

@Component({
  selector: 'app-edit-temporary-purchase',
  templateUrl: './edit-temporary-purchase.component.html',
  styleUrls: ['./edit-temporary-purchase.component.scss']
})
export class EditTemporaryPurchaseComponent implements OnInit {

  invoice: VhPurchaseInvoices = new VhPurchaseInvoices()
  listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  employee: any = {};
  pickerTime = new Date();
  supplier: any;
  printer: any;
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private fncService: FunctionService,
    private cd: ChangeDetectorRef,
    private nzModalService:NzModalService
  ) {
    let invoice = this.router.getCurrentNavigation().extras.state
    if (invoice.invoice) {
      this.invoice = new VhPurchaseInvoices(invoice.invoice, invoice.invoiceDetail);
      this.note = this.invoice.getNote();
      this.pickerTime = new Date(this.invoice.getDate());
    } else {
      // set nhn vin mc nh
      if (!this.invoice.getIdEmployee())
        this.invoice.setIdEmployee(this.vhAuth.getUser()._id)
      this.employee = this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee());
      this.invoice.setIdBranch(this.vhQueryCafe.getDefaultBranch()._id)
    }
    // ------bt s kin nhn phm nng-----
    this.eventKeypress();
  }

  ngOnInit(): void {
    if (this.invoice.getID() && this.listTax.length > 0) {
      this.id_tax = this.listTax.find(item => item.value == this.invoice.getTax())._id;
    } else {
      let tax = this.listTax.find(item => item.default);
      if (tax) {
        this.id_tax = tax._id
        this.invoice.setTax(tax['value']);
      }
    }
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_purchase')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
  }
  event: any // bin lng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
  }
  checkPrinter: boolean = false;
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113 || keycode === 114) event.preventDefault();
      if (keycode === 115) this.searchProductList = !this.searchProductList;
      else if (this.invoice.getInvoiceDetail().length > 0 && this.invoice.getIdPartner()) {
        switch (keycode) {
          case 112: this.payInvoice(2, false)
            break;
          case 113: {
            if (this.checkPrinter) this.payInvoice(2, true)
            break;
          }
        }
      }
    }
    document.addEventListener('keydown', this.event, false);
  }
  ngAfterViewInit() {
    this.checkPrinter = this.printer.enable;
    this.vhAlgorithm.waitingStack().then(() => {
      if (document.querySelector(".discount-price")) this.discount_price = this.vhAlgorithm.vhnumeral(".discount-price");
      if (document.querySelector(".invoice-fee")) this.invoice_fee = this.vhAlgorithm.vhnumeral(".invoice-fee")
    })
    this.cd.detectChanges()
  }
  deleteGoods(detail: VhPurchaseInvoiceDetail, index) {
    this.vhComponent.alertConfirm("", "", `${this.languageService.translate("Delete")} ${detail.name}?`, "OK", this.languageService.translate("Cancel"))
      .then(() => {
        if (!this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_purchase)
          this.invoice.removeInvoiceDetail(detail.id_product)
        else
          this.invoice.getInvoiceDetail().splice(index, 1);
        this.invoice.setSubTotal();
      }, () => { })
  }
  searchProductList: boolean = false;
  openSearchProductList() {
    this.searchProductList = true;
  }
  closeSearchProductList(data) {
    if (data) {
      if (data.manylot && !data.lot_number) this.openModalChooseLot(data)
      else
        if (!this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_purchase)
          this.invoice.addInvoiceDetail(data);
        else this.invoice.addInvoiceDetailMutilLine(data);
    }
    this.searchProductList = false;
  }
  goBack() {
    this.vhComponent.alertConfirm(`${this.languageService.translate("Confirm")}`, "", `${this.languageService.translate("Are you sure to cancel the purchase order")}?`, "OK", this.languageService.translate('Cancel'))
      .then(() => { this.isExit = true; this.router.navigate(["/cafe/dashboard/management/invoice/temporary-purchase-invoice"], { state: this.dataRestore }) }, () => { })
  }
  checkQuantityProduct(product: VhPurchaseInvoiceDetail, event) {
    if (!event) product.quantity = 1;
    this.invoice.setSubTotal();
  }
  changeUnit(ev, item) {
    let unit = this.vhQueryCafe.getUnit_byRatio(item.units, ev);
    if (unit) {
      item.unit = unit.unit
      item.price = parseFloat(unit.price_import)
      item.ratio = parseFloat(unit.ratio)
      item.price_origin = parseFloat(unit.price_import)
    }
    this.invoice.setSubTotal()
  }
  // -------modal edit unit price-------
  isVisibleEditUnitPrice: boolean = false;
  unit_price: any;
  tempDetail: VhPurchaseInvoiceDetail;
  openEditUnitPrice(detail: VhPurchaseInvoiceDetail) {
    this.tempDetail = detail;
    this.isVisibleEditUnitPrice = true;
    this.vhAlgorithm.waitingStack().then(() => {
      this.unit_price = this.vhAlgorithm.vhnumeral('.unit_price');
    });
  }
  handleEditUnit() {
    if (this.unit_price.getRawValue() != '' && this.unit_price.getRawValue() >= 0) {
      this.tempDetail.price = parseFloat(this.unit_price.getRawValue());
      this.invoice.setSubTotal();
      this.isVisibleEditUnitPrice = false;
    } else { this.vhComponent.showToast(2000, this.languageService.translate("Invalid data, please check again"), "alert-toast") }
  }
  handleCancelEditUnit() { this.isVisibleEditUnitPrice = false }
  // -------set employee-------
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.setIdEmployee(event);
    }
  }
  // ----set supplier-----
  closeAndReceiveSupplier(event) {
    if (event) {
      this.invoice.setIdPartner(event);
    }
  }
  closeAndGetValuePayment(event) {
    if (event[0] == 1 || event[0] == 2) {
      this.setPaymentType(event[0])
    }
    else if (event[0] == 3) {
      this.setPaymentType(event[0], event[1]);
    }
  }
  setPaymentType(type: 1 | 2 | 3, id_wallet?) {
    this.invoice.setPaymentType(type)
    switch (type) {
      case 1: this.invoice.setIdWallet('id_cash'); this.invoice.setPayment(); break;
      case 2: this.invoice.setPayment(0); this.invoice.setIdWallet(null); break
      case 3: {
        if (id_wallet) {
          this.invoice.setIdWallet(id_wallet)
          this.invoice.setPayment()
        }
      }
    }
  }
  setPickerTime(event) {

    if (event.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the registration date (")} ${this.fncService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 8000)
      this.pickerTime = new Date(this.invoice.getDate())
      this.invoice.setDate(this.pickerTime);
    }
    else
      this.vhQueryCafe.getValidityDate(new Date(event)).then((validitydate: any) => {
        if (validitydate == null) this.invoice.setDate(event);
        else {
          this.pickerTime = new Date(this.invoice.getDate())
          this.invoice.setDate(this.pickerTime);
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
        }
      })
  }
  // -------change tax------
  id_tax: string;
  changeTax(event) {
    let tax = this.listTax.find(item => item._id == event);
    this.invoice.setTax(tax.value);
  }
  discount_price: any;
  editDiscount() {
    if (this.discount_price.getRawValue() && this.discount_price.getRawValue() >= 0) {
      this.invoice.setDiscount(parseFloat(this.discount_price.getRawValue()));
    } else {
      document.getElementById("discount-price")['value'] = this.invoice.getDiscount();
      this.discount_price = this.vhAlgorithm.vhnumeral('.discount-price');
    }
  }
  invoice_fee: any;
  editFee() {
    if (this.invoice_fee.getRawValue() && this.invoice_fee.getRawValue() >= 0) {
      this.invoice.setFee(parseFloat(this.invoice_fee.getRawValue()));
    } else {
      document.getElementById("invoice-fee")['value'] = this.invoice.getFee();
      this.invoice_fee = this.vhAlgorithm.vhnumeral('.invoice-fee');
    }
  }
  // -------modal edit pay---------
  edit_pay: boolean = false;
  edit_paytotal: any;
  showModalEditTotalPay() {
    if (this.invoice.getPaymentType() != 2) {
      this.vhAlgorithm.waitingStack().then(() => {
        this.edit_paytotal = this.vhAlgorithm.vhnumeral(".edit-pay");
      })
      this.edit_pay = true;
    }
  }
  handleEditPay() {
    if (this.edit_paytotal.getRawValue() >= 0 && this.edit_paytotal.getRawValue() <= this.invoice.getTotalATax()) {
      this.invoice.setPayment(parseFloat(this.edit_paytotal.getRawValue()))
    }
    this.edit_pay = false;
  }
  handleCancelEditPay() {
    this.edit_pay = false;
  }

  handle() {
    this.dataRestore.invoice = this.dataRestore.invoice.filter(item => item._id != this.invoice.getID())
    this.isExit = true;
    this.router.navigate(['/cafe/dashboard/management/invoice/temporary-purchase-invoice'], { state: this.dataRestore })
  }
  note: string;
  edit_note() {
    if (this.note) {
      this.invoice.setNote(this.note)
    }
  }

  //--------------visible drawer info customer-----------
  visibleInfoSupplier: boolean = false;
  openInfoSupplier() {
    if (this.invoice.getIdPartner()) {
      this.supplier = this.vhQueryCafe.getlocalSupplier(this.invoice.getIdPartner());
      this.visibleInfoSupplier = true;
    }
  }
  closeInfoSupplier(event) {
    this.visibleInfoSupplier = false;
  }
  payInvoice(bill_type: 2, print?) {
    if (this.vhAuth.checkMyPermission("purchase_enable_payment")) {
      this.vhComponent.alertConfirm(print ? `${this.languageService.translate("Save & Print")}?` : `${this.languageService.translate("Save")}?`,
        "", "", "OK", this.languageService.translate("Cancel")
      ).then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          var arrayProcess: any[] = [this.createOrUpdate(bill_type)];
          Promise.all(arrayProcess).then(bill => {
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(2000,
                `${this.languageService.translate("Purchase invoice")} ${bill[0].bill_code || this.invoice.getBillCode()} ${this.languageService.translate("has been created successfully")}`, "success-toast"
              )
              if (print) this.handlePrint()
              else this.handle()
            })
          })
        })
      }, () => { })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  createOrUpdate(bill_type: 2) {
    return this.vhQueryCafe.updateBill_Billdetail(this.invoice.getID(), this.invoice.getCreateUpdateInvoice(bill_type), this.invoice.getCreateUpdateInvoiceDetail())
  }

  updateProduct(billDetail: VhPurchaseInvoiceDetail[], arrayUpdate) {
    if (billDetail.length) {
      for (let i in billDetail) {
        arrayUpdate.push(
          this.vhQueryCafe.updateProduct(billDetail[i].id_product, { price_import: billDetail[i].price })
        )
      }
    }
  }


  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_purchase')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }
  handlePrint() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice.getCreateUpdateInvoice() , invoice_detail: this.invoice.getInvoiceDetail()}, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle())
        })
    }, (err) => this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
      .then(() => this.handle()))
  }

  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return (PurchaseK57Col3Component)
      case 'print_size_k80_3c': return (PurchaseK80Col3Component)
      case 'print_size_k80_4c': return (PurchaseK80Col4Component)
      case 'print_size_a5': return (PurchaseA5Component)
      case 'print_size_a4': return (PurchaseA4Component)
      default: return (PurchaseA4Component)
    }
  }
  // ------thot trang-----
  isExit: boolean = false;
  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return nextState.url == '/login' ? true : (this.isExit ? this.isExit : this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Are you sure to cancel the sales order"), "OK", (this.languageService.translate("Cancel"))).then(() => { return true }).catch(() => { return false }))
  }


  show_modal_select_lots = false;
  show_modal_add_lot = false;
  formAddLot: FormGroup;
  disabledAddLot = false;
  list_search_lot = [];

  getLotNumber(lots, _id){
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number  
  } 
  searchLot(value) {
    this.tempDetail.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  openModalChooseLot(data) {
    this.show_modal_select_lots = true;
    this.tempDetail = data ;
    this.tempDetail.days_import_warning = this.vhQueryCafe.getlocalDetailProduct(data.id_subproduct ? data.id_subproduct  : data.id_product).days_import_warning
    this.list_search_lot = this.tempDetail.lots
  }

  changeLot(e) {
    this.tempDetail.lot_number = this.tempDetail.lots.find(item => item._id == e).lot_number
  }

  handleChooseLot() {
    this.tempDetail.lots = this.list_search_lot;
    let lot = this.tempDetail.lots.find(e => e._id == this.tempDetail.id_lotproduct);
    if (lot && this.getNumberOfDay(new Date(lot.date_exp), new Date()) > this.tempDetail.days_import_warning) {
      if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
      this.show_modal_select_lots = false;
    }
    else {
      this.nzModalService.confirm({
        nzTitle: this.languageService.translate("L hng cha  iu kin!"),
        nzContent: this.languageService.translate("S l hng hin ti cha p ng  iu kin nhp hng, yu cu l hng phi c hn s dng cn li ti thiu l") + ' ' + this.tempDetail.days_import_warning + ' ' + this.languageService.translate("days") + '.' + this.languageService.translate("Xc nhn tip tc?"),
        nzOnCancel: () => { this.tempDetail.id_lotproduct = '' },
        nzOnOk: () => {
          this.show_modal_select_lots = false;
          if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
        }
      })
    }
  }
  /**
     * tr v khong cch gia 2 ngy 
     * vd : getNumberOfDay(15/02/2022, 10/02/2022) => return 5
     * */
  getNumberOfDay(date1, date2) {
    return (date1.getTime() - date2.getTime()) / (24 * 3600 * 1000) > 0 ? (date1.getTime() - date2.getTime()) / (24 * 3600 * 1000) : 0
  }

  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot;
    if(!this.tempDetail.lot_number) this.deleteGoods(this.tempDetail, this.invoice.getInvoiceDetail().findIndex(e => e.id_product == this.tempDetail.id_product || e.id_subproduct == this.tempDetail.id_subproduct))
  }

  openAddLot() {
    this.disabledAddLot = false;
    this.show_modal_add_lot = true;
    this.formAddLot = new FormGroup({
      date_mfg: new FormControl(new Date(new Date().setHours(0, 0, 0,)), Validators.compose([Validators.required])),
      date_exp: new FormControl(new Date(new Date().setHours(23, 59, 59,)), Validators.compose([Validators.required])),
      lot_number: new FormControl('', Validators.compose([Validators.required])),
      hidden: new FormControl(false),
      barcode: new FormControl(''),
    })
    this.vhAlgorithm.waitingStack().then(() => {
      document.getElementById('lotNumberError').style.display = 'none';
    })
  }

  exist_barcode = false
  checkBarcode() {
    let detailproduct = this.vhQueryCafe.getlocalDetailProduct_byBarcode_Cafe(this.formAddLot.value.barcode);
    if (detailproduct) {
      this.exist_barcode = true;
      this.disabledAddLot = true;
    }
    else {
      this.disabledAddLot = false;
      this.exist_barcode = false;
    }
  }

  handleAddLot() {
    if (!this.fncService.checkExitsLotNumber(this.formAddLot.value.lot_number)) {
      this.show_modal_add_lot = false;
      console.log(this.tempDetail);
      this.tempDetail.lots = this.tempDetail.lots.concat(this.formAddLot.value)
      if (this.tempDetail.id_subproduct) {
        this.vhQueryCafe.addLotProduct_Cafe(this.tempDetail.id_subproduct,this.formAddLot.value)
          .then((lot: any) => { 
            setTimeout(() => {
              this.tempDetail.lots[this.tempDetail.lots.length -1]._id = lot
              this.tempDetail.lot_number = this.formAddLot.value.lot_number;
              console.log('updateProduct', lot);
              this.tempDetail.id_lotproduct = lot
              this.list_search_lot = this.tempDetail.lots
            }, 200);
          }, error => {
            console.log('error', error);
          })
      }
      else {
        this.vhQueryCafe.addLotProduct_Cafe(this.tempDetail.id_product ? this.tempDetail.id_product : this.tempDetail._id, this.formAddLot.value)
          .then((lot: any) => {
            setTimeout(() => {
              this.tempDetail.lots[this.tempDetail.lots.length -1]._id = lot
              this.tempDetail.lot_number = this.formAddLot.value.lot_number;
              console.log('updateProduct', lot);
              this.tempDetail.id_lotproduct = lot
              this.list_search_lot = this.tempDetail.lots
            }, 200);

          }, error => {
            console.log('error', error);
          })
      }
    }
    else {
      document.getElementById('lotNumberError').style.display = 'block';
      this.vhComponent.alertMessageDesktop('error', this.languageService.translate("S l ny  tn ti"))
    }

  }
}
