import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhQueryCafe, VhAuth, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhPurchaseInvoice, VhPurchaseInvoiceDetail, VhPurchaseInvoices } from 'src/app/cafe/interface/vh-purchase-invoice';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent {

  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhPurchaseInvoices
  dataRestore: any;
  printer: any
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
    let data = this.router.getCurrentNavigation().extras.state
    let print = this.vhQueryCafe.getlocalPrinters().find(item => item.desktop)
    this.printer =print  ? print.desktop.purchase[print.desktop.purchase.type] : {discount_hidden :false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
    this.dataRestore = data.dataRestore
    this.getDataInvoice(data.data._id)
    this.init(data.data.bill_type)
  }

  init(invoice: VhPurchaseInvoice = null, invoiceDetail: VhPurchaseInvoiceDetail[] = null) {
    this.invoice = new VhPurchaseInvoices(invoice, invoiceDetail)
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee()),
        partner: this.vhQueryCafe.getlocalSupplier(this.invoice.getIdPartner()),
        paymentName: this.setPaymentName(this.invoice.getPaymentType())
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        let invoiceDetailFresh: VhPurchaseInvoiceDetail[] = invoiceDetail
        invoiceDetailFresh = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetailFresh, 'name');
        this.init(invoice, invoiceDetailFresh)
        this.vhComponent.hideLoading(0)
      })
    })

  }

  getLotNumber(lots, _id){
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' +  this.fncService.formatDate(lot.date_exp)
  } 

  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("purchase_enable_cancel_bill_temp")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the purchase invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice.getID()).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.languageService.translate("Purchase invoice")} ${this.invoice.getBillCode()} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                this.goBack();
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQueryCafe.getlocalWallet(this.invoice.getIdWallet()).name
    }
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
  public goToCart() {
    if (this.vhAuth.checkMyPermission("purchase_enable_edit_bill_temp")) {
      this.vhQueryCafe.getValidityDate(new Date(this.invoice.getDate())).then((validitydate) => {
        if (validitydate == null) this.router.navigate(['/cafe/dashboard/management/invoice/temporary-purchase-invoice/edit-temporary'], { state: { invoiceDetail: this.invoice.getInvoiceDetail(), invoice: this.invoice.getCreateUpdateInvoice(), dataRestore :this.dataRestore } })
        else this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 4000)
      })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
 
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/invoice/temporary-purchase-invoice'], { state: this.dataRestore });
  }
  ngAfterViewChecked() {
    if (document.querySelector("#temporary-purchase-invoice-detail") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-invoice-detail-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#temporary-purchase-invoice-detail").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-invoice-detail-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }

}
