import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhQueryCafe, VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhPurchaseInvoice } from 'src/app/cafe/interface/vh-purchase-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-temporary-purchase-invoice',
  templateUrl: './temporary-purchase-invoice.component.html',
  styleUrls: ['./temporary-purchase-invoice.component.scss']
})
export class TemporaryPurchaseInvoiceComponent implements OnInit {

  public date: Date[] = [new Date(), new Date()];
  public searchValue: string = ''
  public invoiceShow: any[] = []
  private invoiceFilter: any[] = []
  suppliers = this.vhQueryCafe.getlocalSuppliers()
  public invoices: VhPurchaseInvoice[];
  tableHeight: string;
  totalValInvoice: number = 0;
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  /**Gii hn thi gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30)
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
      this.date = [new Date(this.dataRestore.startTime), new Date(this.dataRestore.endTime)];
      this.invoices = this.dataRestore.invoice
      this.sortFilter(this.filterType)
      this.getMinDate()
      if (this.dataRestore.reload) this.getInvoices()
    }
    else { this.filterType = 'all';  this.getMinDate() }

    //this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoices())
  }
  /**
  * Ly ngy gii hn c xem bo co
 */
  getMinDate() {
    let date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 61)
    this.vhQueryCafe.getValidityDate(date).then((validitydate: Date) => {
      this.minDate = validitydate
    })
  }
  /**
   * get danh sch ha n nhp lu tm
   * @example this.getInvoices()
   */
  getInvoices() {
    this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => {
      this.vhQueryCafe.getBills_byFields({ date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $in: [6] }, id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id } }, {})
        .then((invoice: any[]) => {
          this.invoices = invoice.map(item => {
            return { ...item, employee_name: this.vhAuth.getlocalEmployee(item.id_employee).name, partner_name: this.renderPartner(item.id_supplier), payment_name: this.renderPayment(item.payment_type, item.id_wallet) }
          })
          console.log(this.invoices)
          this.sortFilter(this.filterType)
          this.vhComponent.hideLoading(0);
        }, err => {
          this.vhComponent.hideLoading(0);
        })
    })
  }
  /**
   * tr v tn ncc
   * @example let supplier = this.renderPartner('DKLJWNIUDHWIUHGDIWD')
   */
  renderPartner(id_partner: string) {
    let supplier: string = this.vhQueryCafe.getlocalSupplier(id_partner).name

    return supplier ? supplier : this.languageService.translate("Not found")
  }
  goToDetail(data) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType, startTime: this.date[0], endTime: this.date[1], invoice: this.invoices }
    if (this.vhAuth.checkMyPermission("purchase_enable_view_bill_temp")) this.router.navigate(["/cafe/dashboard/management/invoice/temporary-purchase-invoice/detail"], { state: { data, dataRestore } })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  public filterType = 'all'
  /**
   * lc ha n theo ncc
   * @param filterType 
   */
  sortFilter(filterType ) {
    if(filterType == 'all') {
      this.invoiceFilter = this.invoices
    }
    else{
      this.invoiceFilter = this.invoices.filter(item => item.id_supplier == filterType)
    }

    this.searchBill(this.searchValue)
  }
  /**
   * lc ha n theo ['bill_code', 'partner_name', 'payment_name', 'name_employee', 'status']
   * @param value 
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceFilter, ['bill_code', 'partner_name', 'payment_name', 'name_employee'])
    } else this.invoiceShow = [...this.invoiceFilter]

    this.totalValInvoice = this.getTotal()
  }
  /**
   * tr v tng doanh s ca danh sch ha n ang hin th
   * @returns number
   * @example let total = this.getTotal()
   */
  getTotal() {
    return this.invoiceShow.reduce((prev: number, next) => prev + next.total * (1 + next.tax / 100), 0);
  }


  ngAfterViewChecked() {
    if (document.querySelector("#temporary-purchase-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".invoice-header")) {
      this.tableHeight = document.querySelector("#temporary-purchase-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".invoice-header").clientHeight - 95 + "px";
    }

    this.cdRef.detectChanges();
  }
  /**
   * tr v tn phng thc thanh ton
   * @example let payment = this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.languageService.translate('Cash')
      case 2: return this.languageService.translate('Debit')
      case 3: return this.vhQueryCafe.getlocalWallet(id_wallet).name
    }
  }
  goBack() {
    this.router.navigate(["/cafe/dashboard/management/invoice"])
  }
  copyBillCode(billCode) {
    this.vhComponent.copyValue(billCode)
  }

  /** hm sort cho cc ct */
  dateCol = false;
  bill_codeCol = false;
  partner_nameCol = false;
  payment_nameCol = false;
  name_employeeCol = false;
  sumPriceCol = false;
  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName){
    switch (colName) {
      case 'date':
        if(this.dateCol){
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if(this.bill_codeCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if(this.partner_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'payment_name':
        if(this.payment_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'name_employee':
        if(this.name_employeeCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'sumPrice':
        if(this.sumPriceCol){
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }
  searchFilter(value){
    this.suppliers = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value),this.vhQueryCafe.getlocalSuppliers(),['name'])
  }
}
